<style scoped lang="scss">
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

  .el-form {
    width: 80%;
    margin-top: 50px;
    margin-left: 50px;
  }

  .quill-editor {
    margin-bottom: 100px;
    height: 500px;

    .ql-container {
      height: 500px;
    }

  }

  .limit {
    height: 30px;
    border: 1px solid #ccc;
    line-height: 30px;
    text-align: right;

    span {
      color: #ee2a7b;
    }

  }

  .ql-snow .ql-editor img {
    max-width: 480px;
  }

  .ql-editor .ql-video {
    max-width: 480px;
  }
</style>
<template>
  <div class="wrapper-view">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '' }"><span @click="back">店铺列表</span></el-breadcrumb-item>
      <el-breadcrumb-item>创建店铺</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="店铺名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z2.qiniup.com"
          :show-file-list="false"
          list-type="picture"
          :data="QiniuData"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="form.logo" :src="form.logo" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="店铺地址">
        <el-input v-model="form.address_info"></el-input>
      </el-form-item>
      <el-form-item label="联系方式">
        <el-input v-model="form.tel"></el-input>
      </el-form-item>
      <el-form-item label="商家微信">
        <el-input v-model="form.wechat"></el-input>
      </el-form-item>
      <el-form-item label="商家图片">
        <el-upload
          action="https://upload-z2.qiniup.com"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-success="handleAvatarSuccess2"
          :before-upload="beforeAvatarUpload"
          :data = "QiniuData"
          :file-list="fileList"
            >
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
        </el-dialog>
      </el-form-item>
      <el-form-item label="详情">
        <quill-editor v-model="form.desc"
                      ref="myQuillEditor"
                      :options="editorOption">
        </quill-editor>
        <el-upload class="upload-demo" action="https://upload-z2.qiniup.com"  :data="QiniuData"
                   :on-success='upScuccess' :before-upload="beforeAvatarUpload"
                   ref="upload" style="display:none">
          <el-button size="small" type="primary" id="imgInput"
                     element-loading-text="插入中,请稍候">点击上传
          </el-button>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">确定</el-button>
        <el-button @click="back">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {createStore, editStore, getToken} from '../service'
  import * as Quill from 'quill'

  export default {
    data () {
      return {
        form: {
          name: '',
          logo: '',
          address_info: '',
          tel: '',
          wechat: '',
          desc: '',
          images: ''
        },
        QiniuData: {
          key: '',
          token: ''
        },
        editorOption: {},
        dialogVisible: false,
        dialogImageUrl: [],
        fileList: []
      }
    },
    mounted () {
      if (this.params.name) {
        this.form = this.params
        let picArr = this.params.images
        if (picArr.length) {
          for (let val of picArr) {
            if (val) {
              this.fileList.push({url: val})
            }
          }
        }
      }
      this.getToken()
      // 为图片ICON绑定事件  getModule 为编辑器的内部属性
      this.$refs.myQuillEditor.quill.getModule('toolbar').addHandler('image', this.imgHandler)
    },
    methods: {
      async onSubmit () {
        this.form.images = []
        for (let item of this.fileList) {
          this.form.images.push(item.url)
        }
        let res
        if (!this.params.name) {
          res = await createStore(this.form)
        } else {
          res = await editStore(this.params.id, this.form)
        }
        if (res) {
          this.$message.success('操作成功!')
          this.$emit('show', false)
        } else {
          this.$message.error('操作失败!')
        }
      },
      handleAvatarSuccess (res, file) {
        this.$set(this.form, 'logo', 'http://lhxq.top/' + res.key)
      },
      beforeAvatarUpload (file) {
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!')
        }
        this.QiniuData.key = `${file.name}`
        return isLt2M
      },
      back () {
        this.$emit('show', false)
      },
      async getToken () {
        let res = await getToken()
        this.QiniuData.token = res.data.token
      },
      imgHandler (state) {
        this.addRange = this.$refs.myQuillEditor.quill.getSelection()
        if (state) {
          let fileInput = document.getElementById('imgInput')
          fileInput.click() // 加一个触发事件
        }
      },
      handleRemove (file, fileList) {
        this.fileList = fileList
      },
      // 图片上传成功回调   插入到编辑器中
      upScuccess (e, file, fileList) {
        let vm = this
        let url = 'http://lhxq.top/' + e.key
        if (url != null && url.length > 0) {  // 将文件上传后的URL地址插入到编辑器文本中
          let value = url
          vm.addRange = vm.$refs.myQuillEditor.quill.getSelection()
          value = value.indexOf('http') !== -1 ? value : 'http:' + value
          vm.$refs.myQuillEditor.quill.insertEmbed(vm.addRange !== null ? vm.addRange.index : 0, 'image', value, Quill.sources.USER)   // 调用编辑器的 insertEmbed 方法，插入URL
        } else {
          this.$message.error(`插入失败`)
        }
        this.$refs['upload'].clearFiles()    // 插入成功后清除input的内容
      },
      handlePictureCardPreview (file) {
        this.dialogVisible = true
      },
      handleAvatarSuccess2 (res, file) {
        let str = 'http://lhxq.top/' + res.key
        this.fileList.push({url: str})
      }
    },
    props: ['params']
  }
</script>



