import fetch from '@/config/fetch'

/**
 * 获取redis列表
 */

export const getAdvertList = (page, pageSize) => fetch('/staff/ad/?page=' + page + '&page_size=' + pageSize, {}, 'GET')
export const createAdvert = (data) => fetch('/staff/ad/', {data}, 'POST')
export const deleteAdvert = (id) => fetch('/staff/ad/' + id, {}, 'DELETE')
export const editAdvert = (id, data) => fetch('/staff/ad/' + id + '/', {data}, 'PUT')
export const getToken = () => fetch('/qiniutoken/', {}, 'GET')



