<template>
  <el-row class="tac">
    <el-col :span="12" class="sidebar-col">
      <el-menu
        default-active="2"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        background-color="#515a6e"
        text-color="#fff">
        <el-menu-item index="2" @click="goPage('/manage/goods')">
          <i class="el-icon-menu"></i>
          <span slot="title">
            商品列表
          </span>
        </el-menu-item>
        <el-menu-item index="3"  @click="goPage('/manage/stores')">
          <i class="el-icon-menu"></i>
          <span slot="title">店铺列表</span>
        </el-menu-item>
        <el-menu-item index="4"  @click="goPage('/manage/adverts')">
          <i class="el-icon-menu"></i>
          <span slot="title">广告列表</span>
        </el-menu-item>
      </el-menu>
    </el-col>
  </el-row>
</template>

<style scoped>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: calc(100vh - 64px);
    height: 100%;
  }
  .sidebar-col{
    height: 100%;
  }
</style>

<script>
  export default {
    data () {
      return {
        isCollapse: true
      }
    },
    methods: {
      handleOpen (key, keyPath) {
        console.log(key, keyPath)
      },
      handleClose (key, keyPath) {
        console.log(key, keyPath)
      },
      goPage (page) {
        this.$router.push(page)
      }
    }
  }
</script>



