import login from '@/page/login/router.js'
import manage from '@/page/manage/router.js'

let routes = [login, manage]
let exportRoutes = []
routes.forEach(function (obj, index) {
  if (obj && obj.path) {
    exportRoutes.push(obj)
  }
})

export default exportRoutes



