<style scoped lang="scss">
  .el-form {
    width: 80%;
    margin-top: 50px;
    margin-left: 50px;
  }
  .quill-editor {
    margin-bottom: 100px;
    height: 500px;

  .ql-container {
    height: 500px;
  }

  }

  .limit {
    height: 30px;
    border: 1px solid #ccc;
    line-height: 30px;
    text-align: right;

  span {
    color: #ee2a7b;
  }

  }

  .ql-snow .ql-editor img {
    max-width: 480px;
  }

  .ql-editor .ql-video {
    max-width: 480px;
  }
</style>
<template>
  <div class="wrapper-view">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '' }"><span @click="back">商品列表</span></el-breadcrumb-item>
      <el-breadcrumb-item>创建商品</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="商品名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>

      <el-form-item label="商品名称">
        <el-select v-model="form.store" placeholder="请选择">
          <el-option
            v-for="item in List"
            :key="item.id"
            :label="item.name"
            :value="item.id">
          </el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="产品价格">
        <el-input v-model="form.price" type="number"></el-input>
      </el-form-item>
      <el-form-item label="规格">
        <el-input v-model="form.unit"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          action="https://upload-z2.qiniup.com"
          list-type="picture-card"
          :on-preview="handlePictureCardPreview"
          :on-remove="handleRemove"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload"
          :data = "QiniuData"
          :file-list="fileList">
          <i class="el-icon-plus"></i>
        </el-upload>
        <el-dialog :visible.sync="dialogVisible">
        </el-dialog>
      </el-form-item>
      <el-form-item label="详情">
        <quill-editor v-model="form.detail"
                      ref="myQuillEditor"
                      :options="editorOption">
        </quill-editor>
        <el-upload class="upload-demo" action="https://upload-z2.qiniup.com"  :data="QiniuData"
                   :on-success='upScuccess' :before-upload="beforeAvatarUpload"
                   ref="upload" style="display:none">
          <el-button size="small" type="primary" id="imgInput"
                     element-loading-text="插入中,请稍候">点击上传
          </el-button>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">确定</el-button>
        <el-button @click="back">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {createGood, getStoresList, editGood, getToken} from '../service'
  import * as Quill from 'quill'

  export default {
    data () {
      return {
        form: {
          store: '',
          name: '',
          price: '',
          unit: '',
          detail: '',
          images: []
        },
        List: [],
        QiniuData: {
          key: '',
          token: ''
        },
        editorOption: {},
        dialogVisible: false,
        dialogImageUrl: [],
        fileList: []
      }
    },
    mounted () {
      this.getList()
      this.getToken()
      if (this.params.name) {
        this.form = this.params
        let picArr = this.params.images
        if (picArr.length) {
          for (let val of picArr) {
            if (val) {
              this.fileList.push({url: val})
            }
          }
        }
      }
      this.$refs.myQuillEditor.quill.getModule('toolbar').addHandler('image', this.imgHandler)
    },
    methods: {
      async onSubmit () {
        this.form.images = []
        for (let item of this.fileList) {
          this.form.images.push(item.url)
        }
        let res
        if (!this.params.name) {
          res = await createGood(this.form)
        } else {
          res = await editGood(this.params.id, this.form)
        }
        if (res) {
          this.$message.success('操作成功!')
          this.$emit('show', false)
        } else {
          this.$message.error('操作失败!')
        }
      },

      async getList () {
        let res = await getStoresList()
        this.List = res.data.results
      },
      beforeAvatarUpload (file) {
        // const isJPG = file.type === 'image/jpeg'
        const isJPG = true
        const isLt2M = file.size / 1024 / 1024 < 2

        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG 格式!')
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!')
        }
        this.QiniuData.key = `${file.name}`
        return isJPG && isLt2M
      },
      handlePictureCardPreview (file) {
        this.dialogVisible = true
      },
      handleAvatarSuccess (res, file) {
        let str = 'http://lhxq.top/' + res.key
        this.fileList.push({url: str})
      },
      back () {
        this.$emit('show', false)
      },
      async getToken () {
        let res = await getToken()
        this.QiniuData.token = res.data.token
      },
      upScuccess (e, file, fileList) {
        let vm = this
        let url = 'http://lhxq.top/' + e.key
        if (url != null && url.length > 0) {  // 将文件上传后的URL地址插入到编辑器文本中
          let value = url
          vm.addRange = vm.$refs.myQuillEditor.quill.getSelection()
          value = value.indexOf('http') !== -1 ? value : 'http:' + value
          vm.$refs.myQuillEditor.quill.insertEmbed(vm.addRange !== null ? vm.addRange.index : 0, 'image', value, Quill.sources.USER)   // 调用编辑器的 insertEmbed 方法，插入URL
        } else {
          this.$message.error(`插入失败`)
        }
        this.$refs['upload'].clearFiles()    // 插入成功后清除input的内容
      },
      handleRemove (file, fileList) {
        this.fileList = fileList
      },
      imgHandler (state) {
        this.addRange = this.$refs.myQuillEditor.quill.getSelection()
        if (state) {
          let fileInput = document.getElementById('imgInput')
          fileInput.click() // 加一个触发事件
        }
      },
    },
    props: ['params']
  }
</script>



