import fetch from '@/config/fetch'

/**
 * 获取redis列表
 */

export const getGoodsList = (page, pageSize) => fetch('/staff/product/?page=' + page + '&pageSize=' + pageSize, {}, 'GET')
export const createGood = (data) => fetch('/staff/product/', {data}, 'POST')
export const getStoresList = () => fetch('/staff/store/', {}, 'GET')
export const deleteGood = (id) => fetch('/staff/product/' + id, {}, 'DELETE')
export const editGood = (id, data) => fetch('/staff/product/' + id + '/', {data}, 'PUT')
export const getToken = () => fetch('/qiniutoken/', {}, 'GET')
export const goodUpService = (id) => fetch('/staff/product/' + id + '/set_on_shelf/', {}, 'POST')
export const goodDownService = (id) => fetch('/staff/product/' + id + '/set_off_shelf/', {}, 'POST')



