<template>
  <div id="app">
    <div class="layout">
      <Layout>
        <keep-alive>
          <router-view v-if="$route.meta.keepAlive"></router-view>
        </keep-alive>
        <router-view v-if="!$route.meta.keepAlive"></router-view>
      </Layout>
    </div>
  </div>
</template>

<script>
  import siderBar from '@/components/siderbar/siderBar'
  export default {
    name: 'App',
    components: {
      siderBar
    }
  }
</script>

<style lang="scss">

</style>



