<style lang="scss">
  @import 'header.scss';
</style>
<template>
  <div>
    <Header>
      <Menu mode="horizontal" theme="dark" active-name="1">
        <!--<div class="layout-logo"></div>-->
        <div class="layout-nav" :style="{float:'right'}">
          <MenuItem name="5">
            <div @click="showPasswordChange=true">
              <Icon type="ios-key" title="密码修改" :style="{marginRight:'6px'}"></Icon>
              <span>密码修改</span>
            </div>
          </MenuItem>
          <MenuItem name="6">
            <div @click="showLogout=true">
              <Icon type="ios-log-out" title="注销" :style="{marginRight:'6px'}"></Icon>
              <span>
              注销</span></div>
          </MenuItem>

        </div>
      </Menu>
    </Header>
    <Modal
      v-model="showLogout"
      title="注销"
      width="20%">
      是否注销当前用户?
      <div slot="footer">
        <Button type="default" @click="showLogout=false">取消</Button>
        <Button type="info" @click="showLogout=false;logout()">确定</Button>
      </div>
    </Modal>
    <Modal
      v-model="showPasswordChange"
      title="密码修改"
      width="40%">
      <Row>
        <Form :label-position="'right'" :label-width="100" :model="passwordChange" :rules="rulePasswordChange"
              ref="passwordChange">
          <Row>
            <Col span="16">
              <FormItem label="原密码" prop="oldPassword">
                <Input type="password" placeholder="原密码" v-model="passwordChange.oldPassword"></Input>
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span="16">
              <FormItem label="新密码" prop="newPassword">
                <Input type="password" placeholder="新密码" v-model="passwordChange.newPassword"></Input>
              </FormItem>
            </Col>
          </Row>
          <Row>
            <Col span="16">
              <FormItem label="确认密码" prop="newPassword">
                <Input type="password" placeholder="确认密码" v-model="passwordChange.newPasswordCheck"></Input>
              </FormItem>
            </Col>
          </Row>
        </Form>
      </Row>
      <div slot="footer">
        <Button type="default" @click="showPasswordChange=false">取消</Button>
        <Button type="info" @click="changePassword('passwordChange')">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
  import {logoutService} from '../../page/login/service'

  export default {
    data () {
      return {
        showUserInfo: false,
        tenantList: JSON.parse(localStorage.getItem('user_info')),
        showMessage: false,
        showLogout: false,
        showPasswordChange: false,
        passwordChange: {
          oldPassword: '',
          newPassword: '',
          newPasswordCheck: ''
        },
        rulePasswordChange: {
          oldPassword: [
            {required: true, message: '请填写原密码', trigger: 'blur'},
            // {type: 'string',pattern:/^[0-9A-Za-z]{6,}$/, message: '请填写正确的原密码',trigger: 'blur'},

          ],
          newPassword: [
            {required: true, message: '请填写新密码', trigger: 'blur'},
            // {type: 'string',pattern:/^[0-9A-Za-z]{6,}$/, message: '密码长度不少于6位',trigger: 'blur'},
            {
              type: 'string',
              min: 6,
              max: 16,
              message: '密码长度6-16个字符',
              trigger: 'blur'
            }
          ],
          newPasswordCheck: [
            {required: true, message: '请填写确认密码', trigger: 'blur'},
            {
              type: 'string',
              min: 6,
              max: 16,
              message: '密码长度6-16个字符',
              trigger: 'blur'
            }
            // {type: 'string',pattern:/^[0-9A-Za-z]{6,}$/, message: '密码长度不少于6位',trigger: 'blur'},
            // {type: 'string',pattern:this.passwordChange.newPassword=this.passwordChange.newPasswordCheck, message: '确认密码不一致',trigger: 'blur'},
          ]
        }
      }
    },
    created () {
      // 每次登录之前清理缓存数据
      // this.getTenantMessage()
    },
    methods: {
      showMessageFun () {
        this.showMessage = !this.showMessage
      },

      async logout () {
        let res = await logoutService()
        if (res) {
          this.$router.push('/')
        }
      },
      changePassword (name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            if (this.passwordChange.newPassword === this.passwordChange.newPasswordCheck) {
              const self = this
              let data = self.passwordChange
              // console.log(data);
              self.globalMethod.showLoading('正在修改')
              self.axios.post('/api/user/updatePassword', data).then(function (response) {
                self.globalMethod.dealResponse(response, function () {
                  self.globalMethod.showSuccess('修改成功!')
                  self.showPasswordChange = false
                  self.logout()
                })
              })
            } else {
              this.$Message.error('确认密码填写错误')
            }
          } else {
            this.$Message.error('请填写完整表单')
          }
        })
      }
    }
  }
</script>



