<style lang="scss" scoped>
  .wrapper-view{
    background: #ffffff;width: 100%;padding:20px
  }
</style>
<template>
  <div>
    <tenant-header></tenant-header>
    <div style="display: flex">
      <sider-bar></sider-bar>
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </div>
  </div>
</template>
<script>
  import tenantHeader from '@/components/header/header.vue'
  import siderBar from '@/components/siderbar/siderBar.vue'

  export default {
    components: {
      tenantHeader: tenantHeader,
      siderBar: siderBar
    },
    mounted () {
      this.$router.push('/manage/goods')
    }
  }
</script>



