<template>
  <div class="wrapper-view">
    <div  v-if="!showCreate">
      <el-button type="primary" @click="showCreate = true">创建店铺
      </el-button>
      <div style="min-height: 75vh">
        <el-table
          :data="List"
          style="width: 100%">
          <el-table-column
            prop="name"
            label="店铺名称"
            >
          </el-table-column>
          <el-table-column
            prop="logo"
            label="店铺logo">
            <template   slot-scope="scope">
              <img :src="scope.row.logo"  min-width="70" height="70" />
            </template>
          </el-table-column>
          <el-table-column
            prop="address_info"
            label="店铺地址"
            width="180">
          </el-table-column>
          <el-table-column
            prop="tel"
            label="联系方式">
          </el-table-column>
          <el-table-column
            prop="wechat"
            label="商家微信">
          </el-table-column>
          <el-table-column
            prop="create_at"
            label="创建时间"
            :formatter="this.globalMethod.dateFormat">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button type="primary" size="small" @click="edit(scope.row)">编辑</el-button>
              <el-button type="danger" @click="deleteEntity(scope.row.id)" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-size="pagination.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
    <create-store v-if="showCreate" @show="showFunc" :params="params"></create-store>
  </div>
</template>

<script>
  import {getStoresList, deleteStore} from './service'
  import createStore from './create/create'

  export default {
    data () {
      return {
        List: [],
        pagination: {
          currentPage: 1,
          total: 0,
          pageSize: 5
        },
        showCreate: false,
        params: {}
      }
    },
    created () {
      // this.getList()
    },
    mounted () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    activated () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    methods: {
      async getList (page, pageSize) {
        let res = await getStoresList(page, pageSize)
        this.pagination.total = res.data.count
        this.List = res.data.results
      },
      async deleteStore_ (id) {
        let res = await deleteStore(id)
        if (res) {
          this.getList(this.pagination.currentPage, this.pagination.pageSize)
        }
      },
      createStore () {
        this.$router.push('/manage/stores/create')
      },
      handleSizeChange (val) {
        this.getList(this.pagination.currentPage, val)
      },
      handleCurrentChange (val) {
        this.getList(val, this.pagination.pageSize)
      },
      deleteEntity (id) {
        this.$confirm('确认删除？')
          .then(_ => {
            this.deleteStore_(id)
          })
          .catch(_ => {
          })
      },
      showFunc () {
        this.getList(this.pagination.currentPage, this.pagination.pageSize)
        this.showCreate = false
        this.params = {}
      },
      edit (val) {
        this.showCreate = true
        this.params = val
      }
    },
    components: {createStore}
  }
</script>



