import fetch from '@/config/fetch'

/**
 * 获取redis列表
 */

export const getStoresList = (page, pageSize) => fetch('/staff/store/?page=' + page + '&page_size=' + pageSize, {}, 'GET')
export const createStore = (data) => fetch('/staff/store/', {data}, 'POST')
export const deleteStore = (id) => fetch('/staff/store/' + id, {}, 'DELETE')
export const editStore = (id, data) => fetch('/staff/store/' + id + '/', {data}, 'PUT')
export const getToken = () => fetch('/qiniutoken/', {}, 'GET')



