<template>
  <div class="login">
    <div class="title">
      <h1>莲花商场后台管理</h1>
    </div>
    <div class="from-wrap">
      <h2>登录</h2>
      <Alert type="error" v-if="showList" style="width: 90%;margin-left: 10%;">{{loginMessage}}</Alert>
      <Form ref="loginData" :model="loginData" :rules="ruleValidate" :label-width="80"
            @keyup.enter.native="handleSubmit('loginData')">
        <FormItem label="账号" prop="acct">
          <Input type="text" v-model="loginData.acct" placeholder="请输入账号"></Input>
        </FormItem>
        <FormItem label="密码" prop="pass">
          <Input type="password" v-model="loginData.pass" placeholder="请输入密码"></Input>
        </FormItem>
        <FormItem class="form-footer">
          <Button type="primary" @click="handleSubmit()" long>登录</Button>
          <!--<span class="register" @click="registAcount()" style="margin-top: 15px">还没有账号？马上注册</span>-->
          <!--<Button type="primary" @click="handleReset('loginData')" style="margin-left: 8px">重置</Button>-->
        </FormItem>
      </Form>
    </div>
    <div class="bottom">
      <p>归属：莲花之家</p>
    </div>

  </div>
</template>
<style>
  @import 'login.scss';
</style>
<script>
  import Cookies from 'js-cookie'
  import {loginService} from './service'

  export default {
    data () {
      return {
        showList: false,
        loginMessage: '',
        useList: '',
        loginData: {
          acct: '',
          pass: ''
        },
        ruleValidate: {
          acct: [
            {required: true, message: '账号不能为空', trigger: 'blur'},
            {min: 3, max: 16, message: '账号长度3-16个字符', trigger: 'blur'}
          ],
          pass: [
            {required: true, message: '密码不能为空', trigger: 'blur'},
            {
              type: 'string',
              min: 6,
              max: 16,
              message: '密码长度6-16个字符',
              trigger: 'blur'
            }
          ]
        }
      }
    },
    mounted () {
      if (Cookies.get('isLogin')) {
        this.globalMethod.reWriteUrl(this.$router)
      }
    },
    methods: {
      async handleSubmit () {
        this.globalMethod.showLoading('登录中')
        let param = {
          account: this.loginData.acct,
          password: this.loginData.pass
        }
        let res = await loginService(param)
        this.globalMethod.destroyShow()
        if (res) {
          this.$router.push('manage')
          this.globalMethod.showSuccess('登录成功')
        } else {
          this.globalMethod.showError('登录失败')
        }
      },
      registAcount () {
        this.$router.push('register')
      },
      handleReset (name) {
        this.$refs[name].resetFields()
      },
      getTenantMessage () {
        const self = this
        this.axios({
          method: 'GET',
          url: '/api/user/getUserInfo',
          headers: {token: Cookies.get('token')}
        }).then(response => {
          self.globalMethod.dealResponse(response, function (data) {
            self.useList = response.data.entity
            self.globalMethod.setUserInfo(JSON.stringify(self.useList))
            self.globalMethod.reWriteUrl(self.$router)
          })
        })
      }
    }
  }
</script>



