<template>
  <div class="wrapper-view">
    <div v-if="!showCreate">
      <el-button type="primary" @click="showCreate = true">创建广告
      </el-button>
      <div style="min-height: 75vh">
        <el-table
          :data="List"
          style="width: 100%">
          <el-table-column
            prop="name"
            label="广告名称"
          >
          </el-table-column>
          <el-table-column
            prop="image"
            label="图片">
            <template slot-scope="scope">
              <img :src="scope.row.image" min-width="70" height="70"/>
            </template>
          </el-table-column>
          <el-table-column
            prop="ad_type"
            label="广告类型"
            width="180">
            <template slot-scope="scope">
              <span v-if="scope.row.ad_type == 0">网页</span>
              <span v-if="scope.row.ad_type == 1">店铺</span>
              <span v-if="scope.row.ad_type == 2">产品详情</span>
              <span v-if="scope.row.ad_type == 4">拼车详情</span>
            </template>
          </el-table-column>
          <el-table-column
            prop="create_at"
            label="创建时间"
            :formatter="this.globalMethod.dateFormat">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button type="primary" size="small" @click="edit(scope.row)">编辑</el-button>
              <el-button type="danger" @click="deleteEntity(scope.row.id)" size="small">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>

      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-size="pagination.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
    <create-advert v-if="showCreate" @show="showFunc" :params="params"></create-advert>
  </div>
</template>

<script>
  import {getAdvertList, deleteAdvert} from './service'
  import createAdvert from './create/create'

  export default {
    data () {
      return {
        List: [],
        pagination: {
          currentPage: 1,
          total: 0,
          pageSize: 5
        },
        showCreate: false,
        params: {}
      }
    },
    created () {
      // this.getList()
    },
    mounted () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    activated () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    methods: {
      async getList (page, pageSize) {
        let res = await getAdvertList(page, pageSize)
        this.pagination.total = res.data.count
        this.List = res.data.results
      },
      async deleteAdvert_ (id) {
        let res = await deleteAdvert(id)
        if (res) {
          this.getList(this.pagination.currentPage, this.pagination.pageSize)
        }
      },
      handleSizeChange (val) {
        this.getList(this.pagination.currentPage, val)
      },
      handleCurrentChange (val) {
        this.getList(val, this.pagination.pageSize)
      },
      deleteEntity (id) {
        this.$confirm('确认删除？')
          .then(_ => {
            this.deleteAdvert_(id)
          })
          .catch(_ => {
          })
      },
      showFunc () {
        this.getList(this.pagination.currentPage, this.pagination.pageSize)
        this.showCreate = false
        this.params = {}
      },
      edit (val) {
        this.showCreate = true
        this.params = val
      }
    },
    components: {createAdvert}
  }
</script>



