<style scoped>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
  .el-form {
    width: 40%;
    margin-top: 50px;
    margin-left: 50px;
  }
</style>
<template>
  <div class="wrapper-view">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '' }"><span @click="back">广告列表</span></el-breadcrumb-item>
      <el-breadcrumb-item>创建广告</el-breadcrumb-item>
    </el-breadcrumb>
    <el-form ref="form" :model="form" label-width="80px" class="el-form">
      <el-form-item label="广告名称">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z2.qiniup.com"
          :show-file-list="false"
          list-type ="picture"
          :data="QiniuData"
          :on-success="handleAvatarSuccess"
          :before-upload="beforeAvatarUpload">
          <img v-if="form.image" :src="form.image" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <el-form-item label="广告类型">
          <el-radio v-model="form.ad_type" label="0">网页</el-radio>
          <el-radio v-model="form.ad_type" label="1">店铺</el-radio>
          <el-radio v-model="form.ad_type" label="2">产品详情</el-radio>
          <el-radio v-model="form.ad_type" label="4">拼车详情</el-radio>
      </el-form-item>
      <el-form-item label="上下架">
        <el-date-picker
          v-model="form.end_at"
          type="datetime"
          placeholder="下架日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
        <el-date-picker style="display: inline-block"
          v-model="form.begin_at"
          type="datetime"
          placeholder="上架日期"
          value-format="yyyy-MM-dd HH:mm:ss">
        </el-date-picker>
      </el-form-item>
      <el-form-item label="广告地址">
        <el-input v-model="form.resource_str"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">确定</el-button>
        <el-button @click="back">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import {createAdvert, editAdvert, getToken} from '../service'

  export default {
    data () {
      return {
        form: {
          name: '',
          image: '',
          resource_str: '',
          ad_type: '',
          begin_at: '',
          end_at: '',
          status: 1
        },
        QiniuData: {
          key: '',
          token: ''
        }
      }
    },
    mounted () {
      if (this.params.name) {
        this.form = this.params
        this.form.ad_type = this.form.ad_type + ''
      }
      this.getToken()
    },
    methods: {
      async onSubmit () {
        let res
        if (!this.params.name) {
          res = await createAdvert(this.form)
        } else {
          res = await editAdvert(this.params.id, this.form)
        }
        if (res) {
          this.$message.success('操作成功!')
          this.$emit('show', false)
        } else {
          this.$message.error('操作失败!')
        }
      },
      handleAvatarSuccess (res, file) {
        this.$set(this.form, 'image', 'http://lhxq.top/' + res.key)
      },
      beforeAvatarUpload (file) {
        const isLt2M = file.size / 1024 / 1024 < 2
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!')
        }
        this.QiniuData.key = `${file.name}`
        return isLt2M
      },
      back () {
        this.$emit('show', false)
      },
      async getToken () {
        let res = await getToken()
        this.QiniuData.token = res.data.token
      }
    },
    props: ['params']
  }
</script>



