import manage from '@/page/manage/manage'
import stores from '@/page/stores/router.js'
import goods from '@/page/goods/router.js'
import adverts from '@/page/adverts/router.js'

export default {
  path: '/manage',
  component: manage,
  meta: {
    title: '管理员'
  },
  children: [goods, stores, adverts]
}



