<style scoped>
  .good-img {
    margin-right: 20px;
  }
</style>
<template>
  <div class="wrapper-view">
    <div v-if="!showCreate">
      <el-button type="primary" @click="showCreate = true">创建商品
      </el-button>
      <div style="min-height: 75vh">
        <el-table
          :data="List">
          <el-table-column type="expand">
            <template slot-scope="scope">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="商品图片">
                  <img v-bind:src="item" v-for="item in scope.row.images" width="70" class="good-img"/>
                </el-form-item>
              </el-form>
<!--              <el-form>-->
<!--                <el-form-item label="商品详细">-->
<!--                  <span>{{scope.row.detail}}</span>-->
<!--                </el-form-item>-->
<!--              </el-form>-->
            </template>
          </el-table-column>
          <el-table-column
            prop="name"
            label="商品名称"
            width="180">
          </el-table-column>
          <el-table-column
            prop="price"
            label="价格"
            width="180">
          </el-table-column>
          <el-table-column
            prop="create_at"
            label="创建时间"
            :formatter="this.globalMethod.dateFormat">
          </el-table-column>
          <el-table-column
            prop="view_total"
            label="商品个数">
          </el-table-column>
          <el-table-column
            label="操作" width="300">
            <template slot-scope="scope">
              <el-button type="primary" size="small" @click="edit(scope.row)">编辑</el-button>
              <el-button-group>
                <el-button type="primary" size="small" v-if="scope.row.status === 0" @click="goodUp(scope.row.id)">上架</el-button>
                <el-button type="warning" size="small" v-if="scope.row.status === 1" @click="goodDown(scope.row.id)">下架</el-button>
              </el-button-group>
              <el-button type="danger" size="small" @click="deleteEntity(scope.row.id)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pagination.currentPage"
        :page-size="pagination.pageSize"
        layout="total, prev, pager, next, jumper"
        :total="pagination.total">
      </el-pagination>
    </div>
    <create-good v-if="showCreate" @show="showFunc" :params="params"></create-good>
  </div>
</template>

<script>
  import {getGoodsList, deleteGood, goodUpService, goodDownService} from './service'
  import createGood from './create/create'

  export default {
    data () {
      return {
        List: [],
        pagination: {
          currentPage: 1,
          total: 0,
          pageSize: 5
        },
        showCreate: false,
        params: {}
      }
    },
    created () {
      // this.getList()
    },
    mounted () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    activated () {
      this.getList(this.pagination.currentPage, this.pagination.pageSize)
    },
    methods: {
      async getList (page, pageSize) {
        let res = await getGoodsList(page, pageSize)
        this.List = res.data.results
        this.pagination.total = res.data.count
      },
      async deleteGood_ (id) {
        let res = await deleteGood(id)
        if (res) {
          this.getList(this.pagination.currentPage, this.pagination.pageSize)
        }
      },
      handleSizeChange (val) {
        this.getList(this.pagination.currentPage, val)
      },
      handleCurrentChange (val) {
        console.log(val)
        this.getList(val, this.pagination.pageSize)
      },
      deleteEntity (id) {
        this.$confirm('确认删除？')
          .then(_ => {
            this.deleteGood_(id)
          })
          .catch(_ => {
          })
      },
      showFunc () {
        this.getList(this.pagination.currentPage, this.pagination.pageSize)
        this.showCreate = false
        this.params = {}
      },
      edit (val) {
        this.showCreate = true
        this.params = val
      },
      async goodUp (id) {
        let res = await goodUpService(id)
        if (res) {
          this.globalMethod.showSuccess('上架成功')
        } else {
          this.globalMethod.showError('上架失败')
        }
        this.getList(this.pagination.currentPage, this.pagination.pageSize)
      },
      async goodDown (id) {
        let res = await goodDownService(id)
        if (res) {
          this.globalMethod.showSuccess('下架成功')
        } else {
          this.globalMethod.showError('下架失败')
        }
        this.getList(this.pagination.currentPage, this.pagination.pageSize)
      }
    },
    components: {createGood}
  }
</script>



